import $ from "jquery";
import * as Config from "./Config";

/**
 * 
 * @param  {[type]} apiName      
 * @param  {[type]} param        ,
 * @param  {[type]} type        get/post/delete ,get
 * @param  {[type]} showLoading loading ,
 * @return {[type]}
 */
let request = (apiName, param, type, showLoading) => {
    let url,
        promise,
        paramType = $.type(param);

    if (paramType === "boolean") {
        showLoading = param;
        param = null;
    } else if (paramType === "string") {
        type = paramType;
        param = null;
    }
    if ($.type(type) === "boolean") {
        showLoading = type;
        type = "get";
    }
    showLoading = !!showLoading;
    type = type || "get";
    url = Config.config.api + Config.API.apiUrlPrefix + apiName;
    if (showLoading) {
    	/**/
    }
    promise = $.ajax({
    	url: url, 
    	type: type,
    	data: param, 
    	dataType: "json"
    }).done(function(data) {
        if ($.type(data) === "string") {
            data = $.parseJSON(data);
        }
        return data;
    });
    if (showLoading) {
        promise.always(function() {
        	/**/
    	});
    }

    return promise;
};

/**
 * 
 */
let initConfig = () => {
	let config = Config.config;

	//
	$(document.body).find("[type=\"hidden\"]").each(function(){
		config[$.trim(this.id || this.name)] = $.trim(this.value);
	});

	//
	$.ajaxSetup({
		headers: {
			"Authorization": config.auth
		}
	});
};

/**
 * 
 * @param  {[type]} tpl  Mustach() 
 * @param  {[type]} data  
 * @return {[type]} 
 * @example
 * replaceTpl("Hello, {{world}} And Hello, {{kitty}}", {world: "world!", kitty: "kitty!"})
 */
let replaceTpl = (tpl, data) => {
	data = data || {};

	return (tpl || "").replace(/\{\{(\s*[^\{\}]+\s*)\}\}/g, function(match, name){
		return data[name];
	});
};

/**
 * 
 * @return {[type]} [description]
 */
let getUserInfo = () => {
	return request(Config.API.userInfo).done(function(data){
		$.extend(true, Config.userInfo, data.user);//
	});
};

/**
 * 
 * @return {[type]} [description]
 */
let getQiniuInfo = () => {
	return request(Config.API.qiniu).done(function(data){
        $.extend(true, Config.qiniuInfo, data);//       
		Config.defaultAvatar = data.https_domain + 'img/avatar_default.png';//
	});	
};

/**
 *  /
 * @return {[type]} [description]
 */
let loadBaseInfo = () => {
	return $.when(getUserInfo(), getQiniuInfo());
};

/**
 * 
 * @type {String} 
 */
let promiseCollection = {};
let abortPromise = (name) => {
    if(promiseCollection[name]){
        promiseCollection[name].abort();
        promiseCollection[name] = null;
    }
};

/**
 * 
 * @param  {[type]} list    
 * @param  {String} keyName 
 * @param  {Object} map     
 */
let list2Map = (list, keyName = "id", map = {}) => {
    $.each(list || [], (i, item) => {
        map[item[keyName]] = $.extend(true, {}, item);
    });

    return map;
};

/**
 * Config.userList
 * @param  {[type]} user 
 * @return {[type]} 
 */
let cacheUser = (user) => {
    Config.userList[user.id] = $.extend(true, {}, user);
};

/**
 * id
 * @param  {[type]} id id
 * @return {[type]}    
 */
let getUserFormCacheById = (id) => {
    return Config.userList[id] || {};
};

/**
 * id
 * @type {[type]}
 */
let getIndexById = (list, id) => {
    let index = -1;

    $.each(list, (i, item) => {
        if(item.id == id){
            index = i;
            return false;
        }   
    });

    return index;
};

export {
	request,
    list2Map,
    cacheUser,
	initConfig,
	replaceTpl,
	getUserInfo,
    getIndexById,
	getQiniuInfo,
	loadBaseInfo,
    abortPromise,
    promiseCollection,
    getUserFormCacheById
};



